.card-clean {
  box-shadow: none;

  .card-header {
    padding: 0;

    > *:first-child {
      margin: 0;
      padding: 0;
    }
    &--block {
      padding-bottom: 1em;
    }
  }
  .card-block {
    padding-top: 0;
    padding-bottom: 0;

    &--warning {
      background-color: lighten($honeysuckle-orange, 50%);
      color: darken($honeysuckle-orange, 30%);
      border-radius: 5px 5px 0 0;
      border: 2px solid $honeysuckle-orange;
      border-bottom: 0;

      hr {
        border-top-color: rgba($honeysuckle-orange, .2);
      }
    }
    &--block {
      padding: 2em 2em 0;
    }
  }
  .card-footer {
    border: 0;
    background: none;

    &--warning {
      background-color: lighten($honeysuckle-orange, 50%);
      color: darken($honeysuckle-orange, 30%);
      border-radius: 0 0 5px 5px;
      border: 2px solid $honeysuckle-orange;
      border-top: 0;
    }
    &--block {
      padding: 1em 2em 2em;
    }
  }
}
.card {
  &--full {
    width: 100%;
  }
  &.card-danger {
    .card-heading {
      padding: 1em;

      .title--primary {
        font-size: 1.6em;
        font-weight: 600;
        text-transform: uppercase;
        color: darken($fuchsia-red, 10%);
        padding: 0;
        margin: 0;
      }
    }
    .card-body {
      > *:last-child {
        margin-bottom: 0;
        padding-bottom: 0;
      }
    }
  }
}

.card-default {
  border: 0;
  margin-top: 1.5rem;

  .card-header {
    font-size: 1.6rem;
    color: $main-color;
    padding: 0 0 1rem 0;
    margin-bottom: 1rem;
    background: none;
    border: 0;
    border-bottom: 1px solid $main-color;
  }
  .card-footer {
    display: table;
    width: 100%;
    padding: 15px;
    background: none;
    //border-top: 1px solid $main-color;

    h4 {
      font-size: .9rem;
    }
    pagination,
    .pagination {
      float: left;
    }
    .pagination {
      margin: 0;
    }
  }
}

.card-primary {
  border-radius: 0;
  border-color: $main-color;

  .card-header {
    background-color: $main-color;
    border-color: $main-color;
    border-radius: 0;
    color: $base-light;
  }
  .card-block,
  .card-footer {
    background-color: $base-light;
    border-radius: 0;
  }
}
