.gc-aside {
  position: fixed;
  top: 0;
  right: -300px;
  z-index: 100;
  width: 300px;
  height: 100%;
  height: 100vh;
  display: none;
  background-color: darken($main-color, 10%);
  //@extend .col-helper;
  transform: translateX(0);
  transition: transform .45s ease-in-out;

  &.gc-opened {
    display: block;
    transform: translateX(-300px);
    transition: transform .45s ease-in-out;
  }
  &__heading {
    padding: 1rem;
    color: $base-light;
    background-color: darken($main-color, 12%);

    &__title {
      font-size: 1em;
      font-weight: bold;
      text-transform: uppercase;
      margin-bottom: 0;
    }
  }

  &__list {
    @extend .gc-list;

    > li {
      > a,
      > span {
        background-color: darken($main-color, 10%);
        color: $base-light;
      }
      > a {
        &:hover,
        &:focus {
          background-color: darken($main-color, 15%);
        }
      }
    }
  }

}
