//Colors manual

// Highlight Colors
//
// Colors we use for higlighting states.
//
// $life-green - #3DCD58, primary green
// $logo-green - #009530, secondary green
// $dark-gray - #626469, base gray
//
// Styleguide Base.colors

//Primary Colors
$life-green:   #3DCD58;
$logo-green:   #009530;
$dark-gray:    #626469; //rgba(0,0,0,0.8)
$hyperlight-gray: #f7f7f7;
$white:       #ffffff;

//Secondary Colors
$sky-blue:     #42B4E6;
$medium-gray:   #86949C;
$light-gray:     #9FA0A4; //rgba(0,0,0,0.5)
$background-gray:   #eaeaea;

//Informational Colors
$honeysuckle-orange: #E47F00;
$fuchsia-red: #B10043;
$sunflower-yellow: #FFD100;

//Structural Colors
$base-light: $white;
$base-dark: $dark-gray;
$main-color: $sky-blue;

//Fonts
$font-default: 'Open Sans', sans-serif;
$font-condensed: 'Open Sans Condensed', sans-serif;

//Functional colors
$arrows-icons: $life-green;
