.gc-menu {
  width: $menu-wid;
  height: 100%;
  height: 100vh;
  background-color: $dark-gray;
  color: $base-light;
  transition: width .45s ease-in-out;
  overflow-x: hidden;
  //overflow-y: auto;

  &::-webkit-scrollbar {
    position: absolute;
    width: 10px;
    margin-left: -10px;
    -webkit-appearance: none;
  }
  &::-webkit-scrollbar-thumb {
    height: 50px;
    background-color: darken($dark-gray, 10%);;
    background-clip: content-box;
    border-color: transparent;
    border-style: solid;
    border-width: 1px 2px;
  }
  &::-webkit-scrollbar-track {
    background-color: $dark-gray;
    //border-right: 1px solid darken($dark-gray, 10%);;
    //border-left: 1px solid darken($dark-gray, 10%);;
  }

  a {
    &,
    &:hover,
    &:focus {
      color: $base-light;
    }
  }

  &__header {
    padding: 15px;

    &__brand {
      img {
        width: 100%;
        height: auto;
      }
    }
  }
  &__list {
    @extend .gc-list;

    > li {
      > a,
      > span {
        background-color: darken($dark-gray, 10%);
      }
      > a {
        &:hover,
        &:focus {
          background-color: darken($dark-gray, 15%);
        }
      }
    }
  }
}
.gc-profile {
  padding-top: 2rem;
  font-size: .85rem;
  text-align: center;

  &__title {
    width: calc(#{$menu-wid} - 2rem);
    font-size: .85rem;
  }
  &__name {
    text-transform: uppercase;
    font-size: 1em;
    font-weight: 200;

    b {
      display: block;
      font-size: 1.2em;
    }
  }
}

//Menu aberto
.gc-container--closed {
  &.gc--menu-fixed {
    .gc-menu {
      width: $menu-mini-wid;
      transition: width .45s ease-in-out;
      //overflow-x: inherit;
      overflow: initial;

      &__header {
        min-height: $header-hei;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      &__list {
        li {
          position: relative;

          > a,
          > span {
            padding: 0;
            min-width: $menu-mini-wid;
          }

          &:hover,
          &:focus {
            .gc-menu__list__text {
              display: block;
              width: calc(#{$menu-wid} * 1.2);
              height: $menu-icon-hei;
              background-color: $base-dark;
              overflow: inherit;
              opacity: 1;
              transition: width .45s ease-in-out, opacity .45s ease-in-out;
            }
          }
        }
        &__icon,
        &__text {
          padding: 1rem;
          height: $menu-icon-hei;
        }
        &__icon {
          width: $menu-mini-wid;
          display: inline-block;
          text-align: center;
          font-size: 1.4rem;
          transition: font-size .45s ease-in-out, padding .45s ease-in-out;
        }
        &__text {
          position: absolute;
          top: 0;
          left: $menu-mini-wid;
          width: 0;
          height: 0;
          opacity: 0;
          overflow: hidden;
          transition: width .45s ease-in-out;
        }
      }
    }
    .gc-profile {
      display: none;
    }
    .gc-content {
      width: calc(100% - #{$menu-mini-wid});
      margin-left: $menu-mini-wid;
      transition: width .45s ease-in-out, margin-left .45s ease-in-out;
    }
    .gc-header {
      left: $menu-mini-wid;
      width: calc(100% - #{$menu-mini-wid});
      transition: width .45s ease-in-out, left .45s ease-in-out;
    }
  }
}
