.post-title {
    font-size: 3em;
    text-align: center;
    font-family: fantasy;

    // Styleguide Components.article.{{modifiers}}
    //
    // Include modifiers with the `post-title` CSS.
    //
    // Styleguide Components.article.post-title
    &--main {
      font-size: 4em;
      text-transform: uppercase;
    }
}

