@import "variables";

body {
  background-color: $background-gray;
}
.col-helper {
  position: relative;
  min-height: 1px;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
  float: left;
}
.row-helper {
  width: 100%;
  box-sizing: border-box;

  &:before,
  &:after {
    content: " ";
    display: table;
  }
  &:after {
    clear: both;
  }
}
.gc-container {
  //overflow-x: hidden;
  font-family: $font-default;
  @extend .row-helper;

  //@include grid-collapse;
}

@import "organisms/gc-menu";
@import "organisms/gc-aside";

.gc-content {
  width: calc(100% - #{$menu-wid});
  @extend .col-helper;
  padding: 0;
}
.gc-header {}
.gc-body {
  padding: 1rem;
  @extend .row-helper;

  .breadcrumb {
    font-size: .75rem;
  }
}
.gc-footer {
  width: calc(100% - #{$menu-wid});
  background-color: darken($background-gray, 10%);
  @extend .row-helper;
}
.btn-toggler {
  cursor: pointer;
}
.gc-filter {
  margin-bottom: 1rem;
  display: none;
}
.btn {
  cursor: pointer;
}
.gc-heading {
  &__controls {
    margin-bottom: 2rem;

    .btn {
      margin-right: 1em;

      &:last-child {
        margin-right: 0;
      }
    }
  }
}
.gc-list {
  &:last-child {
    margin-bottom: 0;
  }
  li {
    > a,
    > span {
      display: block;
      min-width: $menu-wid;
      padding: 1rem;
    }
    > a {
      &:hover,
      &:focus {
        text-decoration: none;
      }
    }
  }
  &__icon,
  &__text {
    display: inline-block;
    vertical-align: middle;
  }
  &__icon {
    font-size: 1rem;
    padding: .5rem;
    transition: font-size .45s ease-in-out, padding .45s ease-in-out;
  }
  &__text {
    font-size: .85rem;
  }
}

//Modifiers
//Menu fixed
.gc--menu-fixed {
  overflow:  none;

  .gc-menu {
    position: fixed;
    z-index: 100;
  }
  .gc-header {
    position: fixed;
    top: 0;
    left: $menu-wid;
    width: calc(100% - #{$menu-wid});
    height: $header-hei;
    z-index: 100;
    background-color: $hyperlight-gray;
    transition: width .45s ease-in-out, left .45s ease-in-out;
  }
  .gc-content {
    position: relative;
    top: calc(#{$header-hei});
    height: calc(100vh - #{$header-hei});
    overflow: auto;
    margin-left: $menu-wid;
    box-sizing: border-box;
    transition: width .45s ease-in-out, margin-left .45s ease-in-out;
  }
  .gc-footer {
    @extend .col-helper;
    width: 100%;
    height: #{$footer-hei};
    padding-top: 1rem;
    padding-bottom: 1rem;
    font-size: .75rem;
    color: $dark-gray;
  }
}

